<template>
  <div>
    <header>
      <h1>EF-TEST</h1>
      <span class="byline">Busca las posibles opciones de palabras en un juego de letras</span>
    </header>
    <section>
      <div class="form-container">
        <div class="item-col">
          <el-tooltip
            class="item"
            effect="dark"
            content="Letras disponibles para la busqueda de opciones"
            placement="bottom-end"
          >
            <el-input
              clearable
              placeholder="Ingrese maximo 12 letras "
              maxlength="12"
              v-model="letters"
            />
          </el-tooltip>
        </div>

        <div class="item-col">
          <el-tooltip
            class="item"
            effect="dark"
            content="Numero de letras que contiene la palabra."
            placement="bottom-end"
          >
            <el-input
              clearable
              label="hola"
              type="number"
              placeholder="Ingrese el numero de letras que contiene la palabra"
              v-model="num"
            />
          </el-tooltip>
        </div>
        <div class="item-col">
          <el-tooltip
            class="item"
            effect="dark"
            content="Agregue si desea buscar palabras que empiecen por alguna letra(s) especifica(s)"
            placement="bottom-end"
          >
            <el-input placeholder="Empieza con" clearable v-model="startWith" />
          </el-tooltip>
        </div>
        <div class="item-col">
          <el-button label="Button" type="success" @click="findOptions()">Buscar</el-button>
        </div>
      </div>
    </section>
    <div v-loading="loading" class="results">
      <template v-if="results.length > 0">
        <h1>Resultados</h1>
        <ul>
          <li v-for="word in results" :key="word.id">{{ word.word.toUpperCase() }}</li>
        </ul>
      </template>
      <span v-else-if="searched">No se encontraron resultados</span>
    </div>
    <pagination
      v-show="total > 0"
      :total="total"
      :page.sync="query.page"
      :limit.sync="query.limit"
      @pagination="findOptions"
    />
  </div>
</template>

<script>
import axios from "axios";
import Pagination from "./Pagination";
export default {
  name: "HelloWorld",
  components: { Pagination },
  data() {
    return {
      letters: "",
      searched: false,
      loading: false,
      num: "",
      startWith: "",
      results: [],
      total: 0,
      query: {
        page: 1,
        limit: 500,
      },
    };
  },

  methods: {
    findOptions() {
      this.loading = true;
      this.searched = true;
      axios({
        method: "get",
        url: `/api/search/${this.getLeters()}/${this.getNum()}/${this.getStartWith()}`,
        params: this.query,
      }).then((response) => {
        this.results = response.data.data;
        this.total = response.data.total;
        this.loading = false;
      });
    },
    getStartWith() {
      return this.startWith != "" ? this.startWith : "--";
    },
    getNum() {
      const num = this.num > 2 ? this.num : 2;
      return num;
    },
    getLeters() {
      return this.letters !== "" ? this.letters : "-";
    },
  },
};
</script>

